import './style.css';

export default class Palette {
  constructor() {
    this.bucket = false;
    this.colorPicker = false;
    this.pen = true;
    this.drawing = false;
    this.currentColorValue = '#41F795';
    this.previousColorValue = '#C4C4C4';
    this.canvasEl = document.querySelector('.canvas');
    this.ctx = this.canvasEl.getContext('2d');
    this.bucketToolEl = document.querySelector('.fill-bucket');
    this.colorPickerToolEl = document.querySelector('.choose-color');
    this.penToolEl = document.querySelector('.pencil');
    this.currentColorInput = document.querySelector('.current-color-input');
    this.currentColor = document.querySelector('.current-color');
    this.previousColor = document.querySelector('.previous-color');
    this.canvasEl.width = 16;
    this.canvasEl.height = 16;
    this.keys = {
      p: 80, c: 67, b: 66,
    };
  }

  init() {
    this.addClassActive();
    this.getState();
    this.setCanvasState();
    this.activateTool();
    this.controlKeyboard();
    this.chooseColor();
    this.penTool();
    this.bucketTool();
  }

  // save state

  getState() {
    this.getToolState();
    this.getColorState();
    this.getCanvasState();
  }

  getToolState() {
    this.bucket = JSON.parse(localStorage.getItem('bucket'));
    this.colorPicker = JSON.parse(localStorage.getItem('colorPicker'));
    this.pen = JSON.parse(localStorage.getItem('pen'));
    this.addClassActive();
  }

  getColorState() {
    this.currentColorValue = localStorage.getItem('currentColorValue');
    this.previousColorValue = localStorage.getItem('previousColorValue');
    this.setPaletteColor();
  }

  getCanvasState() {
    const dataURL = localStorage.getItem('canvasImage');
    const img = new Image();
    img.src = dataURL;
    img.addEventListener('load', () => {
      this.ctx.drawImage(img, 0, 0);
    });
  }

  setToolState() {
    localStorage.setItem('bucket', this.bucket);
    localStorage.setItem('colorPicker', this.colorPicker);
    localStorage.setItem('pen', this.pen);
  }

  setColorState() {
    localStorage.setItem('currentColorValue', this.currentColorValue);
    localStorage.setItem('previousColorValue', this.previousColorValue);
  }

  setCanvasState() {
    window.addEventListener('beforeunload', () => {
      localStorage.setItem('canvasImage', this.canvasEl.toDataURL());
    });
  }

  // tools activity

  addClassActive() {
    if (this.bucket) {
      this.removeClassActive();
      this.bucketToolEl.classList.add('active');
      this.currentColorInput.classList.add('hidden');
    }
    if (this.colorPicker) {
      this.removeClassActive();
      this.colorPickerToolEl.classList.add('active');
      this.currentColorInput.classList.remove('hidden');
    }
    if (this.pen) {
      this.removeClassActive();
      this.penToolEl.classList.add('active');
      this.currentColorInput.classList.add('hidden');
    }
  }

  activateTool() {
    this.activateBucketTool();
    this.activateColorPickerTool();
    this.activatePenTool();
  }

  activatePenTool() {
    this.penToolEl.addEventListener('click', () => {
      this.choosePenTool();
    });
  }

  choosePenTool() {
    if (!this.pen) {
      this.removeActiveTool();
      this.pen = !this.pen;
      this.addClassActive();
      this.setToolState();
    }
  }

  activateBucketTool() {
    this.bucketToolEl.addEventListener('click', () => {
      this.chooseBucketTool();
    });
  }

  chooseBucketTool() {
    if (!this.bucket) {
      this.removeActiveTool();
      this.bucket = !this.bucket;
      this.addClassActive();
      this.setToolState();
    }
  }

  activateColorPickerTool() {
    this.colorPickerToolEl.addEventListener('click', () => {
      this.chooseColorPickerTool();
    });
  }

  chooseColorPickerTool() {
    if (!this.colorPicker) {
      this.removeActiveTool();
      this.colorPicker = !this.colorPicker;
      this.addClassActive();
      this.setToolState();
    }
  }

  removeActiveTool() {
    this.bucket = false;
    this.colorPicker = false;
    this.pen = false;
  }

  removeClassActive() {
    this.toolItem = document.querySelectorAll('.tool-item');
    Array.from(this.toolItem).forEach((item) => {
      item.classList.remove('active');
    });
  }

  // Keyboard control

  controlKeyboard() {
    document.addEventListener('keyup', (event) => {
      if (event.keyCode === this.keys.b) { // key "b" activates fill bucket tool
        this.chooseBucketTool();
      }
      if (event.keyCode === this.keys.c) { // key "c" activates color picker tool
        this.chooseColorPickerTool();
      }
      if (event.keyCode === this.keys.p) { // key "p" activates pen tool
        this.choosePenTool();
      }
    });
  }

  // color picker

  chooseColor() {
    this.chooseInputColor();
    this.choosePaletteColor();
    this.chooseCanvasColor();
  }

  chooseInputColor() {
    this.currentColorInput.addEventListener('input', (e) => {
      if (this.colorPicker) {
        const tempValue = this.currentColorValue;
        this.previousColorValue = tempValue;
        this.currentColorValue = e.target.value;
        this.setPaletteColor();
        this.setColorState();
      }
    });
  }

  choosePaletteColor() {
    const toolbarColor = document.querySelector('.toolbar-color');
    toolbarColor.addEventListener('click', (e) => {
      if (this.colorPicker) {
        if (e.target.classList.contains('color')) {
          const tempValue = this.currentColorValue;
          this.previousColorValue = tempValue;
          this.currentColorValue = getComputedStyle(e.target).backgroundColor;
          this.setPaletteColor();
          this.setColorState();
        }
      }
    });
  }

  chooseCanvasColor() {
    this.canvasEl.addEventListener('click', (e) => {
      if (this.colorPicker) {
        const x = e.offsetX;
        const y = e.offsetY;
        const imageData = this.ctx.getImageData(x, y, 1, 1).data;
        const tempValue = this.currentColorValue;
        this.previousColorValue = tempValue;
        this.currentColorValue = `rgba(${imageData[0]},${imageData[1]},${imageData[2]},1)`;
        this.setPaletteColor();
        this.setColorState();
      }
    });
  }

  setPaletteColor() {
    this.currentColor.style.backgroundColor = this.currentColorValue;
    this.previousColor.style.backgroundColor = this.previousColorValue;
  }

  // pen tool

  penTool() {
    this.canvasEl.addEventListener('mousedown', () => {
      this.drawing = true;
    });
    this.canvasEl.addEventListener('mouseup', () => {
      this.drawing = false;
    });
    this.canvasEl.addEventListener('mousemove', (e) => {
      if (this.drawing) {
        if (this.pen) {
          const x = e.offsetX;
          const y = e.offsetY;
          this.ctx.fillStyle = this.currentColorValue;
          this.ctx.fillRect(x, y, 1, 1);
          this.ctx.fill();
        }
      }
    });
  }

  // bucket tool

  bucketTool() {
    this.canvasEl.addEventListener('click', () => {
      if (this.bucket) {
        this.ctx.fillStyle = this.currentColorValue;
        this.ctx.fillRect(0, 0, this.canvasEl.width, this.canvasEl.height);
      }
    });
  }
}
